:root {
  --min-page-width: 1050px;
  --board-font-size: 1rem;
  --battleship-blue: rgb(3, 71, 189);
  --battleship-red: rgb(200, 49, 49);
  --battleship-gray: rgb(182, 200, 201);
  --battleship-gray-light: rgb(225, 241, 241);
}

*,
::before,
::after {
  box-sizing: border-box;
  margin: 0;
}

html,
body {
  height: 100%;
  width: 100%;
}

html {
  font-family: quantico-regular, Roboto, sans-serif;
  color: white;
  font-size: 16px;
  line-height: 1.15; /* Correct the line height in all browsers. */
  -webkit-text-size-adjust: 100%; /* Prevent adjustments of font size after orientation changes in iOS. */
}

body {
  margin: 0;
  min-width: var(--min-page-width);
  background-color: var(--battleship-blue);
}

h1 {
  font-size: 4rem;
  background-color: white;
  padding: 0.5rem 1rem 0 1rem;
  border: 4px solid var(--battleship-blue);
  border-radius: 1rem;
}

h2 {
  font-size: 2rem;
}

p {
  margin: 0;
}

button {
  padding: 5px 10px 1px 10px;
  background-color: white;
  border: 2px solid black;
  border-radius: 8px;
}

button:disabled {
  opacity: 0.3;
}

button:hover:enabled {
  background-color: var(--battleship-gray);
  cursor: pointer;
}

button,
input,
optgroup,
select,
textarea {
  appearance: none;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

button,
[type='button'],
[type='reset'],
[type='submit'] {
  appearance: none;
  -webkit-appearance: button;
}

.page-container {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  width: 100%;
  max-height: 100%;
}

.pregame-page-height {
  gap: 0;
  height: 100%;
}

header {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem 0 0.75rem 0;
  font-family: quantico-bold;
  -webkit-text-stroke: 3px var(--battleship-blue);
  letter-spacing: 5px;
  text-transform: uppercase;
  box-shadow: 0 0rem 0.5rem black;
}

.header-background {
  --s: 4px; /* control the size*/
  --c1: #0347bd;
  --c2: #ffffff;

  --_g: radial-gradient(calc(var(--s) / 2), var(--c1) 97%, #0000);
  background:
    var(--_g),
    var(--_g) calc(2 * var(--s)) calc(2 * var(--s)),
    repeating-conic-gradient(from 45deg, #0000 0 25%, var(--c2) 0 50%)
      calc(-0.707 * var(--s)) calc(-0.707 * var(--s)),
    repeating-linear-gradient(
      135deg,
      var(--c1) calc(var(--s) / -2) calc(var(--s) / 2),
      var(--c2) 0 calc(2.328 * var(--s))
    );
  background-size: calc(4 * var(--s)) calc(4 * var(--s));
}

.pregame-content-container {
  display: flex;
}

.pregame-left {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  gap: 1rem;
  width: 100%;
  height: 100%;
  padding: 1.5rem 1rem;
}

.pregame-left > h2 {
  font-size: 3rem;
}

.pregame-left > p {
  font-size: 1.5rem;
}

.pregame-left > button {
  font-size: 1.5rem;
}

.pregame-right {
  width: 100%;
  padding: 1.5rem 1rem;
}

.ingame-content-container {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.game-container {
  display: flex;
  gap: 1rem;
  width: 100%;
  height: 100%;
  padding: 1rem;
}

.pregame-player-container {
  display: grid;
  grid-template: auto 1fr auto / 1fr 28%;
  row-gap: 1rem;
  column-gap: 1.5rem;
  width: 100%;
  height: auto;
}

.player-container {
  display: grid;
  row-gap: 1rem;
  column-gap: 1.5rem;
  width: 100%;
  height: auto;
}

#p2 {
  grid-template: auto 1fr / 28% 1fr;
}

#p1 {
  grid-template: auto 1fr / 1fr 28%;
}

.player-info {
  grid-row: 1 / 2;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-left: 1.5rem;
  letter-spacing: 3px;
}

#p2 > .player-info {
  grid-column: 2 / 3;
}

.pregame-player-container > .player-info,
#p1 > .player-info {
  grid-column: 1 / 2;
}

.ships-container {
  grid-row: 2 / 3;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: start;
  gap: 1rem;
  width: fit-content;
  padding: 1rem;
}

#p2 > .ships-container {
  grid-column: 1 / 2;
  justify-self: end;
}

.pregame-player-container > .ships-container,
#p1 > .ships-container {
  grid-column: 2 / 3;
  justify-self: start;
}

.ship {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.ship-name {
  display: flex;
}

.ship-name > p {
  height: 1.5rem;
  width: 5rem;
}

.ship-active-indicator {
  position: relative;
  right: 6.7rem;
  bottom: 0.25rem;
  background: url(${___CSS_LOADER_URL_REPLACEMENT_8___}) no-repeat center;
  height: 1.5rem;
  width: 1.5rem;
  margin-left: 2px;
}

.ship-blocks-container {
  display: flex;
}

.ship-block {
  width: 1.5vw;
  min-width: 1rem;
  background-color: var(--battleship-gray);
  border: 0.05rem solid black;
  aspect-ratio: 1;
}

.board-container {
  grid-row: 2 / 3;
  justify-self: center;
  display: grid;
  grid-template: 5% 95% / 5% 95%;
  width: 100%;
  max-width: 600px;
  aspect-ratio: 1;
}

#p2 > .board-container {
  grid-column: 2 / 3;
}

.pregame-player-container > .board-container,
#p1 > .board-container {
  grid-column: 1 / 2;
}

.x-label,
.y-label {
  display: grid;
  font-size: var(--board-font-size);
}

.x-label {
  grid-template: 100% / repeat(10, 10%);
  grid-row: 1 / 2;
  grid-column: 2 / 3;
}

.y-label {
  grid-template: repeat(10, 10%) / 100%;
  grid-row: 2 / 3;
  grid-column: 1 / 2;
}

.x-label > div,
.y-label > div {
  display: flex;
  justify-content: center;
}

.x-label > div {
  align-items: end;
  padding-bottom: 0.25rem;
}

.y-label > div {
  align-items: center;
  padding-right: 0.5rem;
}

.board {
  grid-row: 2 / 3;
  grid-column: 2 / 3;
  display: grid;
  grid-template: repeat(10, 1fr) / repeat(10, 1fr);
  gap: 0;
  background-color: white;
  border: solid 0.1rem black;
  aspect-ratio: 1;
  width: 100%;
}

.board > div {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: var(--board-font-size);
  padding: 0.2rem;
  aspect-ratio: 1;
  width: 100%;
}

.board > div:nth-child(n + 11) {
  border-top: solid 0.05rem black;
}

.board > div:not(:nth-child(10n)) {
  border-right: solid 0.05rem black;
}

.ship-placement-hover,
.ingame-content-container #p2 .board > div:hover {
  background-color: var(--battleship-gray-light);
}

.ship-present {
  background-color: var(--battleship-gray);
}

.pregame-place-ship-nav {
  grid-row: 3 / 4;
  display: flex;
  justify-content: center;
  gap: 1rem;
  padding-top: 1rem;
  padding-left: 1.5rem;
}

.pregame-place-ship-nav > button {
  width: 25%;
  min-width: fit-content;
}

.attack-hit {
  content: url(${___CSS_LOADER_URL_REPLACEMENT_9___});
}

.attack-miss {
  content: url(${___CSS_LOADER_URL_REPLACEMENT_10___});
}

.msg-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  padding: 1rem;
}

#ingame-msg {
  font-size: 1.5rem;
}
